#Application {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #222;
  height: 100vh;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  transition: background-color 3s ease;
}

#quote-box {
  padding: 40px;
  border-radius: 3px;
  width: 600px;
  height: 40%;
  display: block;
  position: relative;
}

#text {
  padding: 60px 5px;

  color: #fff;
  text-align: center;
  transition: color 3s ease;
  font-size: 18px;
}

#line {
  height: 1.5px;
  width: 500px;
  transition: background-color 3s ease;
  margin: auto;
}

#bottom {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: 30px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 10px;
}

#author {
  margin-left: 60px;
  font-size: 20px;
  font-weight: 500;
  transition: color 3s ease;
}

#icons {
  display: flex;
  gap: 40px;
}

#icons img {
  cursor: pointer;
  height: 36px;
  padding: 7px;
  border-radius: 3px;
  transition: background-color 3s ease;
}

button {
  color: #fff;
  font-size: 18px;
  transition: background-color 3s ease;
  border: 0;
  border-radius: 3px;
  padding: 12px;
  width: 100;
  cursor: pointer;
}

@media only screen and (max-width: 1280px) {
  #Application {
    overflow-x: hidden;
    text-align: center;
  }

  #quote-box {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 100px;
    width: 75%;
  }
  #text {
    font-size: 20px;
    margin-bottom: 85px;
    width: 100%;
  }

  #line {
    height: 1.5px;
    margin: auto;
    width: 700px;
    position: absolute;
    top: 30%;
    left: 0;
    right: 0;
    bottom: 0;
  }

  #bottom {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
  }
  #author {
    font-size: 17px;
    padding-bottom: 20px;
    margin: auto;
  }
}

